import React from "react";

// TODO: input tiplerini barındıran bir enum oluşturulacak.
export type InputTypes =
  | "text"
  | "number"
  | "email"
  | "password"
  | "date"
  | "datetime-local"
  | "time"
  | "month"
  | "week"
  | "url"
  | "search"
  | "tel"
  | "color"
  | "file"
  | "range"
  | "hidden"
  | "checkbox"
  | "radio";

interface BaseInputProps {
  type?: InputTypes;
  label?: string;
  id?: string;
  value?: string;
  onChange: React.ChangeEventHandler<HTMLInputElement>;
  placeholder?: string;
  className?: string;
  name?: string;
}

const Input: React.FC<BaseInputProps> = ({
  type = "text",
  label: inputLabel,
  id: inputId,
  value: inputValue,
  onChange: onInputChange,
  placeholder,
  className,
  name,
}) => {
  return (
    <div className="flex flex-col gap-2" aria-roledescription="base-input">
      {inputLabel && (
        <label className="font-medium text-gray-900" htmlFor={inputId}>
          {inputLabel}
        </label>
      )}
      <input
        className={`outline-none ring-1 ring-gray-300 px-2 py-1 rounded focus:ring-indigo-500 focus:ring-2 transition-colors ${
          className || ""
        }`}
        name={name}
        type={type}
        id={inputId}
        value={inputValue}
        onChange={onInputChange}
        placeholder={placeholder}
      />
    </div>
  );
};

export default React.memo(Input);
